import Fbi from './index'
import help from './tasks/help'
import pkg from '../package.json';

export default class cli extends Fbi {
  constructor (argvs) {
    super()
    this.argvs = argvs
    this.tasks = {}

    this.run()
  }

  run () {
    if (!this.argvs.length) {
      return help()
    }

    for (let cmd of this.argvs) {
      switch (cmd) {
        case '-v':
          fbi.utils.log(pkg.version)
          break
      }
    }

    console.log('run: ')
    // console.log(this.argv)
    console.log(this.cfg)
    console.log(this.isFbi)
  }
}
